



























import axios, { AxiosError, AxiosResponse } from "axios";
import { APIConfig } from "../utils/api.utils";
import { Component, Vue } from "vue-property-decorator";
import Footer from "@/components/Footer.vue";
import Header from "@/components/Header.vue";
import Modal from "@/components/Modal.vue";

@Component({
  components: {
    Header,
    Footer,
    Modal
  }
})
export default class Login extends Vue {
  signin: LoginForm = {
    username: "",
    password: ""
  };

  error: string | boolean = false;
  isShowing: boolean = false;

  login() {
    this.error = false;
    axios
      .post(APIConfig.buildUrl("/login"), {
        username: this.signin.username,
        password: this.signin.password
      })
      .then((response: AxiosResponse<LoginResponse>) => {
        this.$store.dispatch("login", {
          token: response.data.token,
          userid: response.data.user.id
        });
        this.$store.state.userID = response.data.user.id;
        this.$store.state.firstName = response.data.user.firstName;
        this.$store.state.admin = response.data.user.admin;
        this.$router.push("orders");
        this.$emit("success");
      })
      .catch((res: AxiosError) => {
        this.isShowing = true;
        this.signin.username = "";
        this.signin.password = "";
        this.error = res.response && res.response.data.error;
      });
  }
}

interface LoginResponse {
  token: string;
  user: User;
}

interface User {
  id: number;
  firstName: string;
  admin: boolean;
}

export interface LoginForm {
  username: string;
  password: string;
}
