














































import { Component, Vue } from "vue-property-decorator";
import Footer from "@/components/Footer.vue";
import AdminHeader from "@/components/AdminHeader.vue";
import axios, { AxiosResponse } from "axios";
import { APIConfig } from "../utils/api.utils";
import { iProduct } from "@/models/product.interface";

@Component({
  components: {
    Footer,
    AdminHeader
  }
})
export default class Orders extends Vue {
  shippingItem: Shipping = {
    firstName: "",
    lastName: "",
    address1: "",
    address2: "",
    city: "",
    state: "",
    zip: ""
  };

  billingItem: Billing = {
    firstName: "",
    lastName: "",
    cardNumber: "",
    expiration: "",
    cvv: ""
  };

  item: Order = {
    orderNumber: 0,
    status: "",
    shippingYN: "",
    orderMonth: 0,
    orderDay: 0,
    orderYear: 0,
    shipping: this.shippingItem,
    billing: this.billingItem,
    prods: this.$store.state.cart
  };

  orders: Order[] = [];

  filteredOrders: Order[] = [];

  stat: string = "Any";

  rev: boolean = false;

  mounted() {
    axios.get(APIConfig.buildUrl("/orders")).then(response => {
      this.orders = response.data.order;
      this.filteredOrders = response.data.order;
    });
  }

  sortByDate() {
    if (this.rev == true) {
      this.rev = false;
    }
    else {
      this.rev = true;
    }
    this.filteredOrders = this.filteredOrders.reverse();
  }

  filterStatus() {
    if (this.stat == "Any") {
      this.filteredOrders = this.orders;
    }
    else {
      this.filteredOrders = [];
      for (let item in this.orders) {
        if (this.orders[item].status == this.stat) {
          this.filteredOrders.push(this.orders[item]);
        }
      }
    }
    if (this.rev == true) {
      this.filteredOrders = this.filteredOrders.reverse();
    }
  }
}

export interface Order {
  orderNumber: number;
  status: string;
  shippingYN: string;
  orderMonth: number;
  orderDay: number;
  orderYear: number;
  shipping: Shipping;
  billing: Billing;
  prods: iProduct[];
}

export interface Billing {
  firstName: string;
  lastName: string;
  cardNumber: string;
  expiration: string;
  cvv: string;
}

export interface Shipping {
  firstName: string;
  lastName: string;
  address1: string;
  address2: string;
  city: string;
  state: string;
  zip: string;
}
