






































































































import { Component, Vue, Prop } from "vue-property-decorator";
import AdminHeader from "@/components/AdminHeader.vue";
import axios, { AxiosResponse } from "axios";
import { APIConfig } from "../utils/api.utils";
import { iProduct } from "@/models/product.interface";
import { Brand } from "@/models/brand.interface";
import { Category } from "@/models/category.interface";

@Component({
  components: {
    AdminHeader
  }
})
export default class ItemEdit extends Vue {
  @Prop() id: string | undefined;
  item: iProduct = {
    id: 0,
    title: "",
    desc: "",
    brand: { id: 1, name: "" },
    categories: [{ id: 1, name: "" }],
    stock: 0,
    cartQuantity: 0,
    price: 0,
    saleYN: false,
    salesPrice: 0,
    canShipYN: false,
    photoURL: "",
    isActive: true
  };
  brands: Brand[] = [];
  categories: Category[] = [];
  selected: number[] = [];

  mounted() {
    if (this.id != "0") {
      axios.get(APIConfig.buildUrl("/shop/" + this.id)).then(response => {
        this.item = response.data.product;
        this.selected = this.item.categories.map(cat => {
          return cat.id;
        });
      });
    }
    axios.get(APIConfig.buildUrl("/brand")).then(response => {
      this.brands = response.data.brands;
    });
    axios.get(APIConfig.buildUrl("/category")).then(response => {
      this.categories = response.data.categories;
    });
  }

  addProduct() {
    this.item.categories = this.selected.map(num => {
      return { id: num, name: "" };
    });

    if (this.id == "0") {
      axios
        .post(APIConfig.buildUrl("/shop"), {
          ...this.item
        })
        .then(res => {
          if (res.status == 200) {
            this.$router.push("/inventory");
          }
        })
        .catch(res => {});
    } else {
      axios
        .put(APIConfig.buildUrl("/shop/" + this.id), {
          ...this.item
        })
        .then(res => {
          if (res.status == 200) {
            this.$router.push("/inventory");
          }
        })
        .catch(res => {});
    }
  }
}
